/* montserrat-500 - cyrillic_latin-ext */
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v26-cyrillic_latin-ext-500.woff2")
      format("woff2"),
    url("../fonts/montserrat-v26-cyrillic_latin-ext-500.ttf") format("truetype");
}
/* montserrat-600 - cyrillic_latin */
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v26-cyrillic_latin-600.woff2") format("woff2"),
    url("../fonts/montserrat-v26-cyrillic_latin-600.ttf") format("truetype");
}
