.scr_scrolling_right_art6 {
  overflow-x: auto;
}

.scr_scrolling_right_art6__wrapper {
  padding: 331px 0 330px;

  @include xl {
    padding: 155px 0 173px;
  }

  @include lg {
    padding: 140px 0 160px;
  }

  @include sm {
    padding: 96px 0 0;
  }

  @include xs {
    padding-top: 143px;
  }
}

.scr_scrolling_right_art6__scroll {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  @include sm {
    display: block;
  }
}

/*=== TITLE ===*/

.scr_scrolling_right_art6__title {
  position: relative;
  margin-right: 71px;
  font-size: 80px;
  font-weight: 600;
  line-height: 140px;
  letter-spacing: -0.02em;
  white-space: nowrap;

  &::after {
    content: "!";
    position: relative;
    left: -18px;

    @include xs {
      display: none;
    }
  }

  @include xl {
    margin-right: 44px;
    font-size: 60px;

    &::after {
      left: -8px;
    }
  }

  @include lg {
    margin-right: 35px;
    font-size: 55px;
  }

  @include sm {
    max-width: 500px;
    margin-right: 0;
    margin-bottom: 19px;
    white-space: initial;

    &::after {
      display: none;
    }
  }

  // first word with blue bg

  .first_word {
    position: relative;
    margin-right: 73px;
    color: $color_white;

    &::after {
      content: "";
      position: absolute;
      display: block;
      top: 50%;
      left: 0;
      z-index: -1;
      width: 97%;
      height: 76px;
      transform: translateY(-50%);
      background-color: $color_blue;
      color: black;
    }

    @include xl {
      margin-right: 50px;

      &::after {
        width: 96%;
        height: 56px;
      }
    }

    @include sm {
      margin-right: 0;

      &::after {
        height: 40px;
      }
    }

    .designer,
    .proger,
    .support,
    .seo,
    .adw {
      position: absolute;
      z-index: 2;

      @include sm {
        display: none;
      }
    }

    .designer {
      top: -76px;
      left: 0;
      width: 99px;
      height: 65px;
      background: url("./../images/scr_scrolling_right_art6/designer.svg")
        center / contain no-repeat;

      @include xl {
        top: -56px;
        left: 3px;
        width: 97px;
        height: 49px;
        background-image: url("./../images/scr_scrolling_right_art6/designer-sm.svg");
      }

      @include lg {
        top: -53px;
        left: 1px;
      }
    }

    .proger {
      top: -76px;
      left: 161px;
      width: 126px;
      height: 65px;
      background: url("./../images/scr_scrolling_right_art6/proger.svg") center /
        contain no-repeat;

      @include xl {
        top: -56px;
        left: 116px;
        width: 123px;
        height: 49px;
        background-image: url("./../images/scr_scrolling_right_art6/proger-sm.svg");
      }

      @include lg {
        top: -53px;
        left: 140px;
      }
    }

    .support {
      bottom: -119px;
      left: 111px;
      width: 201px;
      height: 79px;
      background: url("./../images/scr_scrolling_right_art6/support.svg") center /
        contain no-repeat;

      @include xl {
        width: 174px;
        height: 79px;

        background-image: url("./../images/scr_scrolling_right_art6/support-sm.svg");
      }
    }

    .seo {
      bottom: -119px;
      right: -94px;
      width: 113px;
      height: 75px;
      background: url("./../images/scr_scrolling_right_art6/seo-bottom.svg")
        center / contain no-repeat;

      @include xl {
        width: 110px;
        height: 56px;
        background-image: url("./../images/scr_scrolling_right_art6/seo-bottom-sm.svg");
      }
    }

    .adw {
      bottom: -119px;
      right: -261px;
      width: 118px;
      height: 75px;
      background: url("./../images/scr_scrolling_right_art6/adw.svg") center /
        contain no-repeat;

      @include xl {
        width: 114px;
        height: 56px;
        background-image: url("./../images/scr_scrolling_right_art6/adw-sm.svg");
      }
    }

    @include xl {
      .support {
        bottom: -100px;
        left: 59px;
      }

      .seo {
        bottom: -89px;
        right: -89px;
      }

      .adw {
        bottom: -88px;
        right: -216px;
      }
    }

    @include lg {
      .support {
        left: 53px;
        bottom: -97px;
      }

      .seo {
        bottom: -82px;
        right: -76px;
      }

      .adw {
        bottom: -82px;
        right: -212px;
      }
    }
  }

  // last letter in first word
  .letter {
    position: absolute;
    top: -100%;
    top: unset;
    left: 96.5%;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 76px;
      transform: translateY(-50%);
      background-color: $color_blue;
    }

    .manager {
      position: absolute;
      top: -38px;
      right: -126px;
      z-index: 2;
      width: 137px;
      height: 82px;
      background: url("./../images/scr_scrolling_right_art6/manager.svg") center /
        contain no-repeat;
    }

    @include xl {
      // top: -122%;
      left: 95.4%;

      &::after {
        height: 56px;
        width: 98%;
        left: 1px;
      }

      .manager {
        top: -20px;
        right: -117px;
        width: 122px;
        height: 69px;
        background-image: url("./../images/scr_scrolling_right_art6/manager-sm.svg");
      }
    }

    @include lg {
      // top: -136%;

      .manager {
        top: -16px;
        right: -110px;
        width: 120px;
        height: 64px;
        background: url("./../images/scr_scrolling_right_art6/manager-sm.svg")
          center / contain no-repeat;
      }
    }

    @include sm {
      position: relative;
      top: unset;
      left: unset;

      &::after {
        height: 40px;
      }

      .manager {
        display: none;
      }
    }
  }

  // less than 992px
  @include sm {
    font-size: 40px;
    line-height: 1;
  }

  @include xs {
    font-size: 32px;
    line-height: 40px;
  }
}
/*=== END of TITLE ===*/

/*=== SUBTITLE ===*/
.scr_scrolling_right_art6__subtitle {
  position: relative;
  top: -7px;
  font-size: 80px;
  font-weight: 600;
  line-height: 140px;
  letter-spacing: -0.02em;
  white-space: nowrap;

  @include xl {
    font-size: 60px;
  }

  @include lg {
    font-size: 55px;
  }

  @include sm {
    display: none;
  }

  // first pink figure
  .years_amount {
    position: relative;
    margin-left: 17px;
    margin-right: 21px;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: -1;
      transform: translate(-50%, -50%) rotate(20deg);
      width: 209px;
      height: 214px;
      background: url("./../images/first_scr_main_art6/first-bg-small.svg")
        center / contain no-repeat;
    }

    @include xl {
      margin-left: 13px;
      margin-right: 15px;

      &::after {
        width: 155px;
        height: 159px;
      }
    }
  }

  // color words
  .color_blocks {
    position: relative;
    margin-left: 56px;
    margin-right: 71px;

    .develop,
    .promotion,
    .advertise {
      position: relative;
      z-index: 2;
      color: $color_white;

      &::after {
        content: "";
        position: absolute;
        z-index: -1;
      }
    }

    // blue
    .develop {
      margin-right: 123px;

      &::after {
        top: 10px;
        bottom: 4px;
        left: -14px;
        right: -17px;
        background-color: $color_blue;
      }
    }

    // green
    .promotion {
      &::after {
        top: 10px;
        bottom: 4px;
        left: -14px;
        right: -17px;
        background-color: $color_green;
      }
    }

    // pink
    .advertise {
      position: absolute;
      top: 63%;
      left: 47.6%;
      z-index: 1;
      transform: translateX(-50%);
      color: $color_dark;

      &::after {
        top: 32px;
        bottom: 24px;
        left: -14px;
        right: -17px;
        background-color: $color_pink;
      }
    }

    .proger,
    .manager,
    .seo,
    .adw {
      position: absolute;
      z-index: 2;
    }

    .proger {
      top: -97px;
      left: 251px;
      width: 126px;
      height: 65px;
      background: url("./../images/scr_scrolling_right_art6/proger.svg") center /
        contain no-repeat;

      @include xl {
        width: 123px;
        height: 49px;
        background-image: url("./../images/scr_scrolling_right_art6/proger-sm.svg");
      }
    }

    .manager {
      top: -64px;
      right: -77px;
      z-index: 2;
      width: 137px;
      height: 82px;
      background: url("./../images/scr_scrolling_right_art6/manager.svg") center /
        contain no-repeat;

      @include xl {
        top: -54px;
        width: 122px;
        height: 69px;
        background-image: url("./../images/scr_scrolling_right_art6/manager-sm.svg");
      }
    }

    .seo {
      bottom: 109px;
      right: -50px;
      width: 113px;
      height: 75px;
      background: url("./../images/scr_scrolling_right_art6/seo-top.svg") center /
        contain no-repeat;

      @include xl {
        width: 107px;
        height: 49px;
        background-image: url("./../images/scr_scrolling_right_art6/seo-top-sm.svg");
      }
    }

    .adw {
      bottom: -69px;
      right: 267px;
      width: 118px;
      height: 75px;
      background: url("./../images/scr_scrolling_right_art6/adw.svg") center /
        contain no-repeat;

      @include xl {
        width: 114px;
        height: 56px;
        background-image: url("./../images/scr_scrolling_right_art6/adw-sm.svg");
      }
    }
  }

  .sites_amount {
    position: relative;
    margin-left: 30px;
    margin-right: 28px;
    font-size: 120px;

    &::after {
      content: "";
      position: absolute;
      bottom: 1px;
      left: 0;
      width: 100%;
      height: 10px;
      background-color: $color_blue;
    }

    @include xl {
      font-size: 90px;
    }
  }

  .clients_amount {
    position: relative;
    margin-left: 37px;
    margin-right: 49px;
    font-size: 120px;

    &::after {
      content: "";
      position: absolute;
      top: 55%;
      left: 50%;
      z-index: -1;
      transform: translate(-50%, -50%) rotate(-16deg);
      width: 283px;
      height: 289px;
      background: url("./../images/first_scr_main_art6/first-bg-small.svg")
        center / contain no-repeat;
    }

    @include xl {
      font-size: 90px;
    }
  }

  .years_lines,
  .clients_lines {
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      z-index: 2;
      height: 3px;
      background-color: $color_blue;
    }
  }

  .years_lines {
    &::before {
      width: 153px;
      top: 230px;
      left: -308px;
      transform: rotate(62deg);
    }

    &::after {
      width: 271px;
      top: -203px;
      left: 31px;
      transform: rotate(20deg);
    }
  }

  .clients_lines {
    &::before {
      width: 153px;
      top: -145px;
      left: -373px;
      transform: rotate(-62deg);
    }

    &::after {
      width: 271px;
      top: 284px;
      left: -32px;
      transform: rotate(-20deg);
    }
  }

  .regions_amount {
    position: relative;
    margin-left: 12px;
    margin-right: 8px;
    font-size: 120px;

    &::after {
      content: "";
      position: absolute;
      bottom: 1px;
      left: 0;
      width: 100%;
      height: 10px;
      background-color: $color_blue;
    }

    @include xl {
      font-size: 90px;
    }
  }

  // == SUBTITLE <= 1600px

  @include xl {
    .color_blocks {
      margin-left: 41px;
      margin-right: 53px;

      .develop {
        margin-right: 88px;
      }

      .advertise {
        top: 38%;

        &::after {
          top: 41px;
          bottom: 36px;
          left: -9px;
          right: -13px;
        }
      }

      .manager {
        background: url("./../images/scr_scrolling_right_art6/manager-sm.svg")
          center / contain no-repeat;
      }

      .proger {
        top: -77px;
        left: 157px;
      }

      .seo {
        bottom: 79px;
      }

      .adw {
        bottom: -55px;
        right: 170px;
      }
    }

    .clients_amount {
      margin-left: 31px;

      &::after {
        width: 210px;
        height: 191px;
      }
    }

    .years_lines {
      &::before {
        width: 111px;
        top: 166px;
        left: -227px;
      }

      &::after {
        width: 197px;
        top: -152px;
        left: 28px;
      }
    }

    .clients_lines {
      &::before {
        width: 113px;
        top: -109px;
        left: -271px;
      }

      &::after {
        width: 200px;
        top: 212px;
        left: -20px;
      }
    }

    .regions_amount {
      margin-right: 18px;
    }
  }

  // == SUBTITLE <= 1300px

  @include lg {
    .years_lines {
      &::before {
        width: 100px;
        top: 152px;
        left: -206px;
      }

      &::after {
        width: 180px;
        top: -141px;
      }
    }

    .clients_lines {
      &::before {
        width: 113px;
        top: -109px;
        left: -271px;
      }

      &::after {
        width: 200px;
        top: 212px;
        left: -20px;
      }
    }

    .color_blocks {
      .develop {
        margin-right: 81px;
      }

      .manager {
        top: -52px;
      }

      .advertise {
        top: 33%;
      }
    }

    .sites_amount {
      font-size: 80px;
    }

    .clients_amount {
      font-size: 80px;
    }

    .clients_lines {
      &::before {
        width: 107px;
        top: -103px;
        left: -245px;
      }

      &::after {
        width: 181px;
        top: 197px;
        left: -15px;
      }
    }

    .regions_amount {
      margin-right: 18px;
      font-size: 80px;
    }
  }
}

/*=== END of SUBTITLE ===*/

.scr_scrolling_right_art6__offset {
  min-width: 200px;
  height: 1px;
}

// == For small screen (less then 992px)

.scr_scrolling_right_art6__subtitle-sm {
  display: none;
  max-width: 569px;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: -0.03em;
  color: $color_grey;

  @include sm {
    display: block;
  }

  @include xxs {
    max-width: 360px;
  }
}
