.scr_company_art6 {
  background-color: $color_dark;
  padding-bottom: 128px;

  @include xl {
    padding-bottom: 92px;
  }

  @include lg {
    padding-bottom: 79px;
  }

  @include sm {
    padding-bottom: 68px;
  }

  @include xs {
    padding-bottom: 48px;
  }
}

.scr_company_art6__wrapper {
  position: relative;
  margin-bottom: 53px;

  @include xl {
    margin-bottom: 56px;
  }

  @include lg {
    margin-bottom: 41px;
  }

  @include sm {
    margin-bottom: 38px;
  }

  @include xs {
    margin-bottom: 20px;
  }
}

.scr_company_art6__bg {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 100%;
    padding-top: 45.89%;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include xl {
    &::after {
      padding-top: 45.89%;
    }
  }

  @include sm {
    margin-bottom: 37px;

    &::after {
      // padding-top: 43.39%;
      padding-top: 47.89%;
    }
  }

  @include xs {
    &::after {
      padding-top: 51.69%;
    }
  }
}

.scr_company_art6__person {
  display: flex;
  align-items: flex-end;
  gap: 23px;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: -0.02em;
  color: $color_grey;
  transition: all 1s ease-out;

  &.active {
    color: $color_white;

    .scr_company_art6__post {
      path {
        fill: $color_white;
      }
    }

    .scr_company_art6__name {
      opacity: 1;
      visibility: visible;
      // animation: typing 3.5s steps(28, end);
      // blink-caret .5s step-end infinite;
    }
  }

  &.hidden {
    opacity: 0;
    visibility: hidden;
  }
}

