// arrow right

.scr_company_art6__person--arrow_right {
  .scr_company_art6__name {
    position: relative;
    // overflow: hidden;
    // white-space: nowrap;
    // width: 0;
    // font-family: monospace;
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s ease-out, visibility 1s ease-out;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      background-color: $color_blue;
    }

    &::after {
      content: "";
      position: absolute;
      width: 6px;
      height: 6px;
      top: 5px;
      right: -13px;
      z-index: 1;
      background-color: $color_white;
    }
  }

  .scr_company_art6__post {
    position: relative;
    text-align: right;

    span {
      position: absolute;
      right: -25px;
      bottom: -15px;
      z-index: 1;
      width: 24px;
      height: 26px;

      @include sm {
        right: -20px;
        bottom: 20px;
        transform: rotate(-84deg);
      }

      svg {
        display: block;
        width: 100%;
        height: auto;
      }

      path {
        transition: all 1s ease-out;
      }
    }
  }
}

// arrow left

.scr_company_art6__person--arrow_left {
  .scr_company_art6__name {
    order: 2;
    position: relative;
    // width: 0;
    // font-family: monospace;
    // overflow: hidden;
    // white-space: nowrap;
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s ease-out, visibility 1s ease-out;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      background-color: $color_blue;
    }

    &::after {
      content: "";
      position: absolute;
      width: 6px;
      height: 6px;
      top: 5px;
      left: -13px;
      z-index: 1;
      background-color: $color_white;
    }
  }

  .scr_company_art6__post {
    order: 1;
    position: relative;

    span {
      position: absolute;
      left: -25px;
      bottom: -15px;
      z-index: 1;
      width: 24px;
      height: 26px;

      @include sm {
        right: -20px;
        bottom: 20px;
        transform: rotate(84deg);
      }

      svg {
        display: block;
        width: 100%;
        height: auto;
      }

      path {
        transition: all 1s ease-out;
      }
    }
  }
}

